@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*防止点击闪烁*/
  -webkit-tap-highlight-color: transparent;
  /*缩放网页， 文字大小不变*/
  -webkit-text-size-adjust: none;
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: normal; }

/*修复img默认位置和IE边框*/
img {
  vertical-align: top;
  border: none; }

/*去除虚线*/
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none; }

/*重置部分表单圆角和內阴影*/
input[type="text"],
input[type="file"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="number"],
input[type="email"],
input[type="url"],
textarea {
  -webkit-appearance: none; }

/*去除文本框上下箭头**/
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none; }

/*设置iconfont标签font-famile*/
[class^="icon-"], [class*=" icon-"] {
  font-family: "iconfont";
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  font-size: 14px;
  background-color: #fff; }

li {
  list-style: none; }

a {
  text-decoration: none;
  /*ios 禁止页面在新窗口打开**/
  -webkit-touch-callout: none; }

em, i {
  font-style: normal; }

.ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap; }

.input-noLine {
  height: 80px;
  font-size: 32px; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

.resume-container {
  max-width: 80%;
  margin: 40px auto;
  padding: 0 10px; }

footer {
  margin-top: 30px;
  height: 50px;
  line-height: 50px;
  background-color: #1a1e23;
  text-align: center;
  color: #fff; }



